.video-player__loader {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  position: absolute;
  top: 51%;
  left: 50.2%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  opacity: 0;
}

.video-player__loader.active {
  opacity: 1;
}
.video-player__loader div {
  position: absolute;
  border: 4px solid $accent-color;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.video-player__loader div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}